<template>
  <div class="Echarts">
    <el-button size="small" type="primary" @click="handleInit"></el-button>
    <el-row :gutter="0">
      <el-col class="col" :xs="24" :sm="12" :xl="12">
        <my-pie :optionData="optionDataComputed.ImageAccess" titleText='' :legend='legend.ImageAccess'></my-pie>
      </el-col>
      <el-col class="col" :xs="24" :sm="12" :xl="12">
        <my-pie :optionData="optionDataComputed.onLine" titleText='' :legend='legend.onLine'></my-pie>
      </el-col>
      <el-col class="col" :xs="24" :sm="12" :xl="12">
        <my-pie :optionData="optionDataComputed.transiter" titleText='' :legend='legend.transiter'></my-pie>
      </el-col>
      <el-col class="col" :xs="24" :sm="12" :xl="12">
        <my-pie :optionData="optionDataComputed.playback" titleText='' :legend='legend.playback'></my-pie>
      </el-col>
    </el-row>
    <el-dialog title="" :visible.sync="dialogVisible" width="40%">
      <el-form :model="initForm" label-width="200px">
        <el-form-item label="">
          <el-input v-model="initForm.vedioCapacity" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="initForm.userCapacity" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="initForm.turnCapacity" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="initForm.replayCapacity" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="initSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>

</template>

<script>
import { queryCapacityDatas, initCapacity } from '../../api/api'
import Mypie from './Mypie.vue'
export default {
  name: 'Echarts',
  data () {
    return {
      resData: {},
      optionData: {
        ImageAccess: [],
        onLine: [],
        transiter: [],
        playback: [],
      },
      dialogVisible: false,
      initForm: {
        vedioCapacity: '',
        userCapacity: '',
        turnCapacity: '',
        replayCapacity: ''

      },
      legend: {
        ImageAccess: ['', ''],
        onLine: ['', ''],
        transiter: ['', ''],
        playback: ['', ''],
      }
    }
  },
  components: { 'my-pie': Mypie },
  computed: {
    optionDataComputed: function () {
      var res = this.resData;
      this.optionData.ImageAccess = [
        { value: res.vedioCount ? res.vedioCount : 0, name: '' },
        { value: res.vedioIdleCount ? res.vedioIdleCount : res.vedioCapacity, name: '' }
      ];
      this.optionData.onLine = [
        { value: res.userCount ? res.userCount : 0, name: '' },
        { value: res.userIdleCount ? res.userIdleCount : res.userCapacity, name: '' }
      ];
      this.optionData.transiter = [
        { value: res.turnCount ? res.turnCount : 0, name: '' },
        { value: res.turnIdleCount ? res.turnIdleCount : res.turnCapacity, name: '' }
      ];
      this.optionData.playback = [
        { value: res.reCount ? res.reCount : 0, name: '' },
        { value: res.reIdleCount ? res.reIdleCount : res.replayCapacity, name: '' }
      ];
      return this.optionData
    }
  },
  methods: {
    filterData () {

    },
    handleInit () {
      var self = this;
      self.dialogVisible = true;
    },
    initSubmit () {
      var self = this;
      initCapacity(this.initForm).then(res => {
        self.$message({
          type: "success",
          message: ""
        })
        self.dialogVisible = false;

      })
    }
  },
  mounted () {
    queryCapacityDatas({ 'id': 1 }).then(res => {
      this.resData = res;
      this.initForm.vedioCapacity = res.vedioCapacity
      this.initForm.userCapacity = res.userCapacity
      this.initForm.turnCapacity = res.turnCapacity
      this.initForm.replayCapacity = res.replayCapacity

    })
    console.log(this.optionData)
  }
}
</script>

<style>
.col {
  border: 1px solid #333;
  margin-top: 20px;
}
</style>



